@import url(https://fonts.googleapis.com/css2?family=Advent+Pro:wght@200;400;500;700&display=swap);
body {
  font-family: 'Advent Pro', sans-serif;
  font-size: 16px;
  color: #666665;
}

h1 {
  font-size: 54px;
  font-weight: bold;
}

h2 {
  font-size: 40px;
  font-weight: 700;
}

h4 {
  font-size: 32px;
}

h5 {
  font-size: 24px;
}

img {
  display: block;
  width: 100%;
}

strong {
  font-weight: 700;
}

p {
  letter-spacing: 0.34px;
  line-height: 22px
}

.myButton {
  display: block;
  background-color: #FF576F;
  border-radius: 50px;
  padding: 8px 24px;
  font-size: 18px;
  font-weight: 500;
  color: white;
  text-transform: uppercase;
  text-align: center;
}

.myButton.mini{
  padding: 8px 16px;
  font-size: 12px;

}

footer {
  height: 60px;
  width: 100%;
}

footer p {
  color: #B39898;
  font-size: 10px;
  font-weight: 500;
  letter-spacing: 0;
  line-height: 12px;
  text-align: center;
}

.menu {
    background-color: white;
    padding: 24px 0;
}

.flex-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.to-link {
    text-transform: uppercase;
    font-size: 14px;
    color: #B39898;
    font-weight: 500;
    margin: 0 12px;
}

.to-link:hover {
    color: #6EA8B5;
    text-decoration: underline;
}
.homePage {
    background-image: url('/imgs/home/bkg.svg');
    background-size: cover;
}

.homePage h1 {
    color: #B39898;
}

.homePage h2 {
    color: #B39898;
}

.homePage h4 {
    color: #B39898;
}

.homePage h5 {
    color: #D9A5A6;
    font-weight: 200;
}

.homePage .logo {
    width: 286px;
    margin: 64px 0 40px -21px;
}

.homePage .imgHeader {
    width: 576px;
    margin: 380px 0 0;
}


.homePage .features {
    font-size: 18px;
    color: #D9A5A6;
    font-weight: 500;
}

.homePage .myButton {
    margin-top: 40px;
    width: 188px;
}

.homePage .infoSection {
    padding: 200px 0;
}

.homePage .infoSection .imgInfo {
    width: 364px;
}

.homePage .howWorks {
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    padding-bottom: 104px;
}

.homePage .howWorks .groupCards {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 48px;
}

.homePage .howWorks .groupCards .eachCard {
    height: 278px;
    width: 258px;
    border: 6px solid #FCCCD2;
    border-radius: 10px;
    background-color: #FFFFFF;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 40px 36px 60px;
    text-align: center;
    margin-right: 24px;
}

.homePage .howWorks .groupCards .eachCard .icons {
    width: 18px;
}

.homePage .howWorks .groupCards .eachCard .icons img {
    object-fit: contain;
    object-position: top;
}


.signup-page {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.signup-page h4 {
    color: #B39898;
    margin: 48px 0;
}

.signup-page .my-form {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.signup-page .form-button {
    background-color: #FF576F;
    border-radius: 50px;
    text-transform: uppercase;
    font-weight: 500;
    font-size: 16px;
    padding: 8px 24px;
    border: none;
    margin: 32px 0 16px;
}

.signup-page .form-button:hover {
    background-color: #6EA8B5;
}

.signup-page .form-label {
    font-size: 14px;
    margin: 4px 0;
}

.signup-page .form-input {
    background-color: rgba(252, 204, 210, 0.1);
    border: none;
    border-bottom: solid #FCCCD2 2px;
    width: 368px;
}

.signup-page .form-input::-webkit-input-placeholder {
    font-size: 12px;
}

.signup-page .form-input:-ms-input-placeholder {
    font-size: 12px;
}

.signup-page .form-input::-ms-input-placeholder {
    font-size: 12px;
}

.signup-page .form-input::placeholder {
    font-size: 12px;
}

.signup-page .mini-link {
    color: #6EA8B5;
}
.login-page {
    display: flex;
    flex-direction: column;
    align-items: center;

}

.login-page h4 {
    color: #B39898;
    margin: 48px 0;
}

.login-page .my-form {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.login-page .form-button {
    background-color: #FF576F;
    border-radius: 50px;
    text-transform: uppercase;
    font-weight: 500;
    font-size: 16px;
    padding: 8px 24px;
    border: none;
    margin: 32px 0 16px;
}

.login-page .form-button:hover {
    background-color: #6EA8B5;
}

.login-page .form-label {
    font-size: 14px;
    margin: 4px 0;
}

.login-page .form-input {
    background-color: rgba(252, 204, 210, 0.1);
    border: none;
    border-bottom: solid #FCCCD2 2px;
    width: 368px;
}

.login-page .form-input::-webkit-input-placeholder {
    font-size: 12px;
}

.login-page .form-input:-ms-input-placeholder {
    font-size: 12px;
}

.login-page .form-input::-ms-input-placeholder {
    font-size: 12px;
}

.login-page .form-input::placeholder {
    font-size: 12px;
}

.login-page .mini-link {
    color: #6EA8B5;
}
.latNavBar {
    background-color: #FDFBFB;
    width: 256px;
    height: 95vh;
    border-radius: 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    background-image: url('/imgs/dashboard/nav-bkg.svg');
    background-repeat: no-repeat;
    background-position: bottom center;
    position: fixed;
}

.latNavBar .logoDash {
    width: 147px;
    margin-top: 24px;
}

.latNavBar .personalInfo {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.latNavBar .personalInfo .logoProfile {
    width: 77px;
    height: 77px;
    margin-top: 36px;
    border-radius: 50px;
    overflow: hidden;
}

.latNavBar .personalInfo .logoProfile img {
    object-fit: contain;
    object-position: bottom;
}

.latNavBar .personalInfo .dashName {
    color: #B39898;
    font-size: 22px;
    font-weight: 500;
    margin: 0;
}

.latNavBar .personalInfo .dashEmail {
    color: #B39898;
    font-size: 18px;
    margin: 0;
    margin-bottom: 40px;
}

.latNavBar .dashLink {
    border: none;
    width: inherit;
    padding: 16px 24px;
    color: #B39898;
    font-size: 18px;
    font-weight: 700;
    text-transform: capitalize;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.latNavBar .buttonGroup {
    width: inherit;
}

button {
    background-color: transparent;
}

.latNavBar .dashLink:hover {
    background-color: white;
    border-left: #FCCCD2 5px solid;
    border-right: #FCCCD2 5px solid;
    text-decoration: none;
    padding: 16px 19px;

}

.latNavBar .dashLink img {
    width: 15px;
}
.profilePage {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    margin-left: 256px;
    padding: 32px 64px;
}

.profilePage h4 {
    color: #B39898;
}

.profilePage .profileImg {
    width: 150px;
    height: 150px;
    overflow: hidden;
    border-radius: 100px;
}

.profilePage .profileImg img {
    object-fit: cover;
    object-position: center;
}
.homeDash {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    margin-left: 256px;
    background-image: url('/imgs/home/bkg.svg');
    background-size: cover;
    padding-bottom: 200px;
}

.homeDash h4 {
    color: #B39898;
}

.homeDash .welcomeBox {
    width: 98%;
    height: 300px;
    border-radius: 20px;
    background-color: #FDFBFB;
    padding: 32px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 16px;
}

.homeDash .welcomeBox p {
    width: 400px;
    margin-top: 24px;
}

.homeDash .homePortfolios,
.homeDash .homeCv {
    border: 6px solid #FDFBFB;
    background-color: rgba(255, 255, 255, 0.9);
    border-radius: 20px;
    padding: 32px;
    margin: 16px 0;
    width: 98%;
    display: flex;
    flex-direction: column;
}


.homeDash .homePortfolios .everyPortfolio .eachPortfolio,
.homeDash .homeCv .everyCv .eachCv {
    background-color: #FDFBFB;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    border-radius: 10px;
    padding: 24px;
    width: 245px;
    margin: 24px 24px 0 0;
}

.homeDash .homePortfolios .everyPortfolio .eachPortfolio:hover,
.homeDash .homeCv .everyCv .eachCv:hover {
    border: 6px solid #B39898;
    padding: 18px;
    color: #B39898;
}

.homeDash .homePortfolios .everyPortfolio .eachPortfolio.add,
.homeDash .homeCv .everyCv .eachCv.add {
    background-color: #F5F6F9;
}

.homeDash .homePortfolios .everyPortfolio .eachPortfolio figure,
.homeDash .homeCv .everyCv .eachCv figure {
    width: 44px;
    height: 44px;
    overflow: hidden;
    margin: 0;
}

.homeDash .homePortfolios .everyPortfolio .eachPortfolio img,
.homeDash .homeCv .everyCv .eachCv img {
    object-fit: cover;
    object-position: center;
}

.homeDash .homePortfolios .eachPortfolio .titlePortfolio,
.homeDash .homeCv .everyCv .eachCv .titleCv {
    color: #FCCCD2;
    text-transform: uppercase;
    font-weight: 700;
    font-size: 21px;
    margin: 0;
    text-align: right;
}

.homeDash .homePortfolios .eachPortfolio .titlePortfolio:hover,
.homeDash .homeCv .everyCv .eachCv .titleCv:hover {
    color: #B39898;
    text-decoration: none;
}

.homeDash .homePortfolios .eachPortfolio .addPortfolio,
.homeDash .homeCv .everyCv .eachCv .addCv {
    color: #F0D1D2;
    font-size: 18px;
    letter-spacing: 0.34px;
    line-height: 22px;
    margin: 0;
}
.cvPage {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    margin-left: 256px;
    padding: 32px 64px;
}
.dashPage {
    display: flex;
    justify-content: space-between;
    max-height: 95vh;
    overflow: scroll;
    margin-top: 16px;
}
